import { createSelector } from '@reduxjs/toolkit';
// Ð¿Ñ€Ð¾ÑÑ‚Ñ‹Ðµ ÑÐµÐ»ÐµÐºÑ‚Ð¾Ñ€Ñ‹
export const getContacts = state => state.contacts.items;
export const getLoading = state => state.contacts.isLoading;
export const getError = state => state.contacts.error;
export const getFilter = state => state.contacts.filter;
//Ð¼ÐµÐ¼Ð¾Ð¸Ð·Ð°Ñ†Ð¸Ñ.
export const getVisibleContacts = createSelector(
  [getContacts, getFilter], //Ð°Ñ€Ð³ÑƒÐ¼ÐµÐ½Ñ‚Ñ‹. ÑÑÑ‹Ð»ÐºÐ¸ Ð¾Ñ‚ Ñ‡ÐµÐ³Ð¾ Ð·Ð°Ð²Ð¸ÑÐ¸Ñ‚
  //Ñ„-Ñ†Ð¸Ñ Ð´ÐµÐ»Ð°ÐµÑ‚ Ð²Ñ‹Ñ‡Ð¸ÑÐ»ÐµÐ½Ð¸Ñ. Ð¿Ð¾Ð»ÑƒÑ‡Ð°ÐµÑ‚ Ñ€ÐµÐ·ÑƒÐ»ÑŒÑ‚Ð°Ñ‚ Ð²Ñ‹Ð·Ð¾Ð²Ð° Ð¿Ñ€Ð¾ÑÑ‚Ñ‹Ñ… ÑÐµÐ»ÐµÐºÑ‚Ð¾Ñ€Ð¾Ð²
  (contacts, filter) => {
    const normalizedFilter = filter.toLowerCase();

    return contacts.filter(contact =>
      contact.name.toLowerCase().includes(normalizedFilter),
    );
  },
);
